import React, { Component } from 'react';
import pdf from '../images/Praveen Resume-edited.pdf';
import { Grid, Cell } from 'react-mdl';

class Resume extends Component {
  render() {
    return (
      <div>
        <Grid>
          <Cell col={12} style={{ textAlign: 'center' }}>
            <h1>Get My Resume</h1>
            <i
              class='fa fa-arrow-circle-down'
              style={{ fontSize: '40px', color: '#c471ed' }}
            />
            <br />
            <br />

            <a
              href={pdf}
              style={{
                textDecoration: 'none',
                fontSize: '20px'
              }}
              rel='noopener noreferrer'
              target='_blank'
            >
              Resume Pdf
            </a>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Resume;
