import React, { Component } from 'react';
import bg from '../images/bg.JPG';
import {
  Tabs,
  Tab,
  Grid,
  Cell,
  Card,
  CardTitle,
  CardText,
  CardActions,
  Button,
  CardMenu,
  IconButton
} from 'react-mdl';

class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }
  toggleCategory() {
    if (this.state.activeTab === 0) {
      return (
        <div className='projects-grid'>
          <Card shadow={5} style={{ width: '350px', margin: 'auto' }}>
            <CardTitle
              style={{
                color: '#fff',
                height: '176px',
                background: `url(${bg}) center / cover`
              }}
            >
              Resume
            </CardTitle>
            <CardText>My Portfolio Website</CardText>
            <CardActions border>
              <Button colored>GitHub</Button>
              <Button colored>Show Project</Button>
            </CardActions>
            <CardMenu style={{ color: '#fff' }}>
              <IconButton name='share' />
            </CardMenu>
          </Card>
          <Card shadow={5} style={{ width: '350px', margin: 'auto' }}>
            <CardTitle
              style={{
                color: '#fff',
                height: '176px',
                background: `url(${bg}) center / cover`
              }}
            >
              Resume
            </CardTitle>
            <CardText>My Portfolio Website</CardText>
            <CardActions border>
              <Button colored>GitHub</Button>
              <Button colored>Show Project</Button>
            </CardActions>
            <CardMenu style={{ color: '#fff' }}>
              <IconButton name='share' />
            </CardMenu>
          </Card>
          <Card shadow={5} style={{ width: '350px', margin: 'auto' }}>
            <CardTitle
              style={{
                color: '#fff',
                height: '176px',
                background: `url(${bg}) center / cover`
              }}
            >
              Resume
            </CardTitle>
            <CardText>My Portfolio Website</CardText>
            <CardActions border>
              <Button colored>GitHub</Button>
              <Button colored>Show Project</Button>
            </CardActions>
            <CardMenu style={{ color: '#fff' }}>
              <IconButton name='share' />
            </CardMenu>
          </Card>
        </div>
      );
    } else if (this.state.activeTab === 1) {
      return (
        <div>
          <h1>This is Angular Project</h1>
        </div>
      );
    } else if (this.state.activeTab === 2) {
      return (
        <div>
          <h1>This is JavaScript Project</h1>
        </div>
      );
    } else if (this.state.activeTab === 3) {
      return (
        <div>
          <h1>This is CSS Project</h1>
        </div>
      );
    }
  }
  render() {
    return (
      <div className='category-tabs'>
        <Tabs
          activeTab={this.state.activeTab}
          onChange={tabId => this.setState({ activeTab: tabId })}
          ripple
        >
          <Tab>React</Tab>
          <Tab>Angular</Tab>
          <Tab>JavaScript</Tab>
          <Tab>CSS</Tab>
        </Tabs>
        <section>
          <Grid>
            <Cell col={12}>{this.toggleCategory()}</Cell>
          </Grid>
        </section>
      </div>
    );
  }
}

export default Projects;
