import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import avatar from '../images/13CSR132.jpg';
import Education from '../Components/education';
import Skills from '../Components/skills';

class About extends Component {
  render() {
    return (
      <div>
        <Grid>
          <Cell col={4}>
            <div style={{ textAlign: 'center' }}>
              <img src={avatar} alt='Avatar' />
            </div>
            <h2 style={{ paddingTop: '1em' }}>Praveen Kumar</h2>
            <h4 style={{ color: 'grey' }}>Web Developer</h4>
            <hr style={{ borderTop: '3px solid #833fb2', width: '50%' }} />
            <p>
              Proident mollit veniam non adipisicing dolore cillum veniam. Nisi
              deserunt culpa reprehenderit magna esse dolore tempor voluptate ex
              aute est minim ea. Nostrud incididunt consectetur incididunt magna
              quis laborum fugiat aliquip nisi qui consequat pariatur eu. Elit
              officia fugiat magna amet aliquip sint. Culpa pariatur esse eu
              occaecat culpa reprehenderit aute enim esse culpa minim labore
              est.
            </p>
            <hr style={{ borderTop: '3px solid #833fb2', width: '50%' }} />
            <h5>Address</h5>
            <p>5,Veeri St, Rasipuram,Namakkal(Dt),PIN-637408</p>
            <hr style={{ borderTop: '3px solid #833fb2', width: '50%' }} />
            <h5>Phone</h5>
            <p>(+91) 979 1234 627</p>
            <hr style={{ borderTop: '3px solid #833fb2', width: '50%' }} />
            <h5>Email</h5>
            <p>praveenprakasham@gmail.com</p>
            <hr style={{ borderTop: '3px solid #833fb2', width: '50%' }} />
          </Cell>
          <Cell className='resume-right-col' col={8}>
            <h2 style={{ marginLeft: '0.4em' }}>Education</h2>
            <hr style={{ borderTop: '3px solid #f64f59', width: '70%' }} />
            <Education
              startYear={1999}
              endYear={`April 2011`}
              degree={`Matriculation(HSC)`}
              schoolName={`Shri Vidya Mandir Hr Sec School at Rasipuram, Namakkal`}
              cgpa={`73.8%`}
            />
            <Education
              startYear={`July 2011`}
              endYear={`May 2013`}
              degree={`Matriculation(SSLC)`}
              schoolName={`Shri Vidya Mandir Hr Sec School at Rasipuram, Namakkal`}
              cgpa={`77.08%`}
            />
            <Education
              startYear={`July 2013`}
              endYear={`May 2017`}
              degree={`Bachelors in Computer Science and Engineering`}
              schoolName={`Kongu Engineering College`}
              cgpa={`60.00%`}
            />
            <hr style={{ borderTop: '3px solid #f64f59', width: '70%' }} />
            <h2 style={{ marginLeft: '0.4em' }}>Skills</h2>
            <hr style={{ borderTop: '3px solid #f64f59', width: '70%' }} />
            <Skills skill={`HTML5/CSS3`} progress={85} />
            <Skills skill={`JAVASCRIPT`} progress={70} />
            <Skills skill={`REACTJS`} progress={60} />
            <Skills skill={`BOOTSTRAP`} progress={80} />
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default About;
